.App-logo {
  height: 85vmin;
  pointer-events: none;
}

.App-header {
  margin-top:24px;
  background-color: #2F4F4F;
  min-height: 40vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-bottom: 50px;
  
}
.App-footer {
  background-color: #2C3531;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  
  width:100%;
  position:fixed;
  background-color:	#2C3531;
  height: 2.5rem;
  bottom: 0px;
  left: 0px;
  
  
}

.App-link {
  color: #61dafb;
}

